<template>
 <div class="singer" ref="singer">
     <list-view :data="singers" @select="selectSinger" ref="singerView"></list-view>
     <router-view></router-view>
 </div>
</template>

<script type="text/ecmascript-6">
  import ListView from 'base/listview/listview'
  import {getSingerList} from 'api/singer'
  import Singer from 'common/js/singer'
  import {ERR_OK} from 'api/config'
  import { mapMutations } from 'vuex'
  import { playListMixin} from 'common/js/mixin'

  const HOT_NAME="热"
  const HOT_SINGERS=10
 export default{
      mixins:[playListMixin],
     data(){
         return{
             singers:[]
         }
     },
   components:{
         ListView
   },
   created(){
         this._getSingerList();
   },
   methods:{
       handlePlayList(playList){
           const bottom  = playList.length >0 ? '60px' :''
           this.$refs.singer.style.bottom = bottom
           this.$refs.singerView.refresh()
       },
       _getSingerList(){
          getSingerList().then((res) => {
             if(res.code===ERR_OK)
             {
               this.singers=this._normalizeSinger(res.data.list);
             }
          })
       },
     _normalizeSinger(list){/*处理获取到的数据*/
           let map={
               hot:{
                   title:HOT_NAME,
                   items:[]
               }
           }
           list.forEach((item,index) => {
               if(index < HOT_SINGERS)
               {
                 map.hot.items.push(new Singer({id:item.Fsinger_mid,name:item.Fsinger_name}))
               }
              const key=item.Findex;
               if(!map[key])
               {
                   map[key]={
                       title:key,
                       items:[]
                   }
               }
               map[key].items.push(new Singer({id:item.Fsinger_mid,name:item.Fsinger_name}))
           })
          /*为了得到有序列表处理map*/
          let hot=[];
          let ret=[];
          for(let key in map)
          {
              let val=map[key];
              if(val.title.match(/[a-zA-Z]/))
              {
                ret.push(val)
              }
              else if(val.title===HOT_NAME)
              {
                  hot.push(val)
              }
          }
          ret.sort((a,b) => {
              return a.title.charCodeAt(0)-b.title.charCodeAt(0)
          })
       return hot.concat(ret)
     },
     selectSinger(singer){
        this.$router.push({
            path:`/singer/${singer.id}`
        })
       this.setSinger(singer);
     },
     ...mapMutations({
         setSinger:"SET_SINGER"
     }),
   }
 };
</script>

<style lang="stylus" rel="stylesheet/stylus">
.singer
  position: fixed
  top: 88px
  bottom: 0px
  width:100%
</style>



