<template>
  <scroll class="listview" :data="data" :listenScroll="listenScroll" :probeType="probeType" ref="listview" @scroll="onScroll">
    <ul>
      <li v-for="group in data" class="list-group" ref="listgroup" >
        <h2 class="list-group-title">{{group.title}}</h2>
        <ul>
          <li class="list-group-item" v-for="item in group.items" @click="selectItem(item)">
            <img v-lazy="item.avatar" class="avatar">
            <span class="name">{{item.name}}</span>
          </li>
        </ul>
      </li>
    </ul>
    <div class="list-shortcut" @touchstart="onShortcutTouchStart" @touchmove.stop.prevent="onShortcutTouchMove">
      <ul>
        <li v-for="(item,index) in shortcutList" class="item" :data-index="index" :class="{current:currentIndex===index}">
          {{item}}
        </li>
      </ul>
    </div>
    <div class="list-fixed" v-show="fixedTitle" ref="fixed">
      <h1 class="fixed-title">{{fixedTitle}}</h1>
    </div>
    <div v-show="!data.length" class="loading-container">
      <loading></loading>
    </div>
  </scroll>
</template>

<script type="text/ecmascript-6">
  import Scroll from 'base/scroll/scroll'
  import Loading from 'base/loading/loading'
  import {getData} from 'common/js/dom.js'
  const ANCHOR_HEIGHT=18/*字母表中的每个字母的高度*/
  const TITLE_HEIGTH=30
  export default{
      data(){
          return {
              scrollY:-1,
              currentIndex:0,
              diff:-1
          }
      },
      created(){
          /*定义在组件中需要共享的属性*/
          this.touch={};
          this.listenScroll=true;
          this.listHeight=[];
          this.probeType=3;/*设置允许连的滚动*/
          this.fixedTop=0;
      },
      props:{
          data:{
              type:Array,
              default:null
          }
      },
    computed:{
          shortcutList(){
              return this.data.map((group) => {
                  return group.title
              })
          },
         fixedTitle(){
              if(this.scrollY>0)
              {
                  return ''
              }
              return this.data[this.currentIndex]?this.data[this.currentIndex].title:''
         }
    },
    components:{
          Scroll,
          Loading
    },
    methods:{
      onShortcutTouchStart(e){
             let anchorIndex=parseInt(getData(e.target,'index'));
             let firstTouch=e.touches[0];
             this.touch.y1=firstTouch.pageY;
             this.touch.anchorIndex=anchorIndex;
             if(!anchorIndex&&anchorIndex!==0){/*当点击到空白区域时，不改变当前高亮的索引*/
               return
             }
             this.currentIndex=anchorIndex;
             this.scrollToIndex(anchorIndex);
          },
      onShortcutTouchMove(e){
             let firstTouch=e.touches[0];
             this.touch.y2=firstTouch.pageY;
             let delta=(this.touch.y2-this.touch.y1)/ANCHOR_HEIGHT|0;
             let anchorIndex= this.touch.anchorIndex+delta;
             if(anchorIndex<0){/*当滚动到空白区域时，不改变当前高亮的索引*/
               anchorIndex=0;
             }
             else if(anchorIndex>this.listHeight.length-2)
             {
                 anchorIndex=this.listHeight.length-2;
             }
             this.currentIndex=anchorIndex;
             this.scrollToIndex(anchorIndex);
      },
      refresh(){
          this.$refs.listview.refresh()
      },
      scrollToIndex(index){
        this.$refs.listview.scrollToElement(this.$refs.listgroup[index],0)
      },
      onScroll(pos){
          this.scrollY=pos.y;
      },
      _calculateHeight(){/*计算高度实现两边的联动*/
             this.listHeight=[];
             const list=this.$refs.listgroup;
             let height=0;
             this.listHeight.push(height);
             for(let i=0;i<list.length;i++)
             {
                 let item=list[i];
                 height+=item.clientHeight;
                 this.listHeight.push(height)
             }
      },
      selectItem(item){
          this.$emit("select",item)
      }
    },
    watch:{
      data(){
        setTimeout(() => {/*数据变化，dom在下一个tick变化，本应该用this.$nextTick但是为了确保手机的兼容性，利用的是setTimeout*/
          this._calculateHeight();
        },20)

      },
      scrollY(newY){
        const listHeight=this.listHeight;
        //当滚动到顶部，newY>0
        if(newY>0)
        {
            this.currentIndex=0;
            return
        }
        /*当滚动到区间内*/
        for(let i=0;i<listHeight.length-1;i++)
        {
          let height1=listHeight[i];
          let height2=listHeight[i+1]
          if(!height2||(-newY>=height1&&-newY<height2))
          {
            this.currentIndex=i;
            this.diff=height2+newY;
            return
          }
          //当滚动到底部，且-newY大于最后一个元素的上限
          this.currentIndex=listHeight.length-2;
        }
      },
      diff(newVal){
          let fixedTop=(newVal>0 && newVal<TITLE_HEIGTH)?newVal-TITLE_HEIGTH:0;
          if(this.fixedTop===fixedTop){
              return
          }
          this.fixedTop=fixedTop;
          this.$refs.fixed.style.transform=`translate3d(0,${fixedTop}px,0)`;
      }
    },
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .listview
    position: relative
    width: 100%
    height: 100%
    overflow: hidden
    background: $color-background
    .list-group
      padding-bottom: 30px
      .list-group-title
        height: 30px
        line-height: 30px
        padding-left: 20px
        font-size: $font-size-small
        color: $color-text-l
        background: $color-highlight-background
      .list-group-item
        display: flex
        align-items: center
        padding: 20px 0 0 30px
        .avatar
          width: 50px
          height: 50px
          border-radius: 50%
        .name
          margin-left: 20px
          color: $color-text-l
          font-size: $font-size-medium
    .list-shortcut
      position: absolute
      z-index: 30
      right: 0
      top: 50%
      transform: translateY(-50%)
      width: 20px
      padding: 20px 0
      border-radius: 10px
      text-align: center
      background: $color-background-d
      font-family: Helvetica
      .item
        padding: 3px
        line-height: 1
        color: $color-text-l
        font-size: $font-size-small
        &.current
          color: $color-theme
    .list-fixed
      position: absolute
      top: 0
      left: 0
      width: 100%
      .fixed-title
        height: 30px
        line-height: 30px
        padding-left: 20px
        font-size: $font-size-small
        color: $color-text-l
        background: $color-highlight-background
    .loading-container
      position: absolute
      width: 100%
      top: 50%
      transform: translateY(-50%)
</style>



